
import React, {  useContext } from 'react';
import styled, { keyframes } from 'styled-components';
import {  Grid, GridItem,  Button , Avatar} from '../../style/components'
import {  mobileWidth, primaryColor } from '../../config';

const slide = keyframes` from {transform: translateX(22em)} to {transform: translateX(0)}`;
const TopNavBar = ({ user, logout}: any) => {

  return (
        <MiniNav>
          <div className="capitalize bold txt-md" style={{ minWidth: "100px" }}>
            Analytics
          </div>
          <GridItem hideOnMobile={true} style={{ gridColumnStart: 10 }}>
                Search
          </GridItem>
          <div className="grid" style={{ gridColumnStart: 19, gridTemplateColumns: "auto auto auto" }}>
            <div>
             <i style={{fontSize: "2.5em"}} className="fa fa-car"></i>
             <i className="fa fa-plus"></i>
            </div>

            <div>
             <i style={{fontSize: "2em"}} className="fa fa-bullhorn"></i>
             <i className="fa fa-plus"></i>
            </div>
         </div>

         <div className="grid" style={{ gridColumnStart: 20 }}>
              <Avatar url={"https://f0.pngfuel.com/png/136/22/profile-icon-illustration-user-profile-computer-icons-girl-customer-avatar-png-clip-art.png"}/>
         </div>

        </MiniNav>)
}


const Nav = styled.div`
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    font-size: .8em; 
    display: grid;
    padding: .3em;
    position: fixed;`;

const TopNav = styled(Nav)`
    right : 0;
    bottom: 0;
    height: 100vh;
    width: 25em;
    font-size: 15px;
    grid-template-rows: repeat(20, auto);
    background: inherit;  
    top: 0;
    place-items: center;
    z-index: 9;
    @media (max-width: ${mobileWidth}) {
      height: 70px;
      width: 100vw;
      top : inherit;
      top: 0;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 1fr;}`;

const topNav = `
left: 0;
width: 100vw;
height: 4em; 
color: ${primaryColor};
background: inherit;
padding-left: 15em;
grid-template-columns: repeat(20, auto);
grid-template-rows: 1fr;`;

const MiniNav = styled(TopNav)`
  width: 5em;
    ${topNav}
    @media(max-width: ${mobileWidth}){
      padding-left: 0
      grid-template-columns: repeat(5, auto);
    } `;

export default TopNavBar;