import React from 'react';
import styled from 'styled-components';

const ImgBox = styled.img`
border: 2px solid teal;
border-radius: 50%;
object-fit: cover;
width: 9em;
filter: drop-shadow(1px 2px 3px #045258);
height: 9em;`;


export const Avatar = ({ url, src, large, style, medium }: any) => {
    style = {
        ...style, ... large? {
            borderWidth: '4px',
            width: '15em',
            maxHeight: "90vw",
            height: '15em'
        } : !medium? {
            width: '3em',
            height: '3em'
        }: {}
    };
  return (<ImgBox src={url || src} style={style} />);
}

export default Avatar;