import styled from 'styled-components';
import React from 'react';
import { primaryColor, txtColor, txtColorDark,  cardColorDark} from '../../config';
import { sizeStyle, fontStyle, designStyle, elementProps, gridStyle } from '../generator';

export const Card = (props: any) => {
    const { border, normalBg, children,  bgColor, dark } = props;
    const { invert, grid, bgReg, selected, hover, margin, padding } = props;
    const darkMode = dark || false;
    const elProps = elementProps(props);
    
    const Card = styled.div`
        box-shadow: ${selected ? '0 .25em .5em dodgerblue' : '0 .25em .5em rgba(0,0,0,.5)'};
        ${hover && '&:hover { transform: scale(1.1); }'}
        ${border && `border: ${border} solid;`}

        ${bgColor &&  `background: ${bgColor}; !important`}
        background: ${darkMode ? cardColorDark : invert ? primaryColor : "white"}};
        ${bgReg && `background: ${darkMode ? cardColorDark : `url(${bgReg});`}
        ${!normalBg && `background-size: cover;
                        background-attachment: fixed;`}`}
        ${grid && gridStyle(props)}
        ${sizeStyle({ ...props, ...{ margin: margin || "1em .5em", padding: padding || ".8em 1em" } })}
        ${fontStyle(props, darkMode)}
        ${designStyle(props)}
        color: ${invert ? txtColorDark: darkMode? txtColorDark : txtColor};`;

    return (<Card {...elProps}>{children}</Card>);
};

