import React, { useMemo, useCallback, useState, useEffect } from 'react';
import { AreaClosed, Line, Bar} from '@vx/shape';
import { curveMonotoneX } from '@vx/curve';
import { GridRows, GridColumns } from '@vx/grid';
import { scaleTime, scaleLinear } from '@vx/scale';
import { withTooltip, Tooltip, defaultStyles } from '@vx/tooltip';
import { WithTooltipProvidedProps } from '@vx/tooltip/lib/enhancers/withTooltip';
import { localPoint } from '@vx/event';
import { LinearGradient } from '@vx/gradient';
import { max, extent, bisector } from 'd3-array';
import { timeFormat } from 'd3-time-format';
import { primaryColor } from '../../config';
import { AxisLeft, AxisBottom } from '@vx/axis';

type Data = {
  date: string,
  value: number
};

export const background =  primaryColor;
export const background2 = primaryColor;
export const accentColor = "lightgray";
export const accentColorDark = '#75daad';

const tooltipStyles = {
  ...defaultStyles,
  background,
  border: '1px solid white',
  color: 'white',
};

const formatDate = timeFormat("%b %d, '%y");
const getDate = (d: Data) => new Date(d.date);
const getStockValue = (d: Data) => d.value;
const bisectDate = bisector<Data, Date>(d => new Date(d.date)).left;

export type AreaProps = {
  width: number;
  height: number;
  stock : any;
  margin?: { top: number; right: number; bottom: number; left: number };
};

export default withTooltip<AreaProps, Data>(({
    width,
    stock,
    height,
    margin = { top: 0, right: 0, bottom: 0, left: 0 },
    showTooltip,
    hideTooltip,
    tooltipData,
    tooltipTop = 0,
    tooltipLeft = 0,
  }: AreaProps & WithTooltipProvidedProps<Data>) => {
    if (width < 10) return null;

    const xMax = width - margin.left - margin.right;
    const yMax = height - margin.top - margin.bottom;

  

    const dateScale = useMemo(() =>
        scaleTime({
          range: [0, xMax],
          domain: extent(stock, getDate) as [Date, Date],
        }),
      [xMax],
    );
    const stockValueScale = useMemo(() =>
        scaleLinear({
          range: [yMax, 0],
          domain: [0, (max(stock, getStockValue) || 0) + yMax / 3],  
        }),
      [yMax],
    );

    const handleTooltip = useCallback(
      (event: React.TouchEvent<SVGRectElement> | React.MouseEvent<SVGRectElement>) => {
        const { x } = localPoint(event) || { x: 0 };
        const x0 = dateScale.invert(x);
        const index = bisectDate(stock, x0, 1);
        const d0 = stock[index - 1];
        const d1 = stock[index];
        let d = d0;
        if (d1 && getDate(d1)) {
          d = x0.valueOf() - getDate(d0).valueOf() > getDate(d1).valueOf() - x0.valueOf() ? d1 : d0;
        }
        showTooltip({
          tooltipData: d0,
          tooltipLeft: x,
          tooltipTop: stockValueScale(getStockValue(d)),
        });
      },
      [showTooltip, stockValueScale, dateScale],
    );
    return (
              <div>
        <svg width={width} height={height}>
          <rect
            x={0}
            y={0}
            width={width}
            height={height}
            fill="url(#area-background-gradient)"
            rx={14}
          />
      
          <LinearGradient id="area-background-gradient" from={background} to={background2} />
          <LinearGradient id="area-gradient" from={accentColor} to={accentColor} toOpacity={0.1} />
          <GridRows<number>
            scale={stockValueScale}
            width={xMax}
            strokeDasharray="3,3"
            stroke={accentColor}
            strokeOpacity={0.3}
            pointerEvents="none"
          />
          <GridColumns<Date>
            scale={dateScale}
            height={yMax}
            strokeDasharray="3,3"
            stroke={accentColor}
            strokeOpacity={0.3}
            pointerEvents="none"
          />
          <AreaClosed<Data>
            data={stock}
            x={d => dateScale(getDate(d))}
            y={d => stockValueScale(getStockValue(d))}
            yScale={stockValueScale}
            
            strokeWidth={1}
            stroke="url(#area-gradient)"
            fill="url(#area-gradient)"
            curve={curveMonotoneX}
          />
          <Bar
            x={0}
            y={0}
            width={width}
            height={height}
            fill="transparent"
            rx={14}
           
            onTouchStart={handleTooltip}
            onTouchMove={handleTooltip}
            onMouseMove={handleTooltip}
            onMouseLeave={() => hideTooltip()}
          />
          {tooltipData && (
            <g>
              <Line
                from={{ x: tooltipLeft, y: 0 }}
                to={{ x: tooltipLeft, y: yMax }}
                stroke={accentColorDark}
                strokeWidth={2}
                pointerEvents="none"
                strokeDasharray="5,"
              />
            </g>
          )}
            <AxisBottom
          hideTicks={true}
          scale={dateScale}
          tickStroke={primaryColor}
          stroke={primaryColor}
          tickLabelProps={(value : any, index: any) => ({
            fontSize: 11,
            stroke: "white",
            dx: '15px',
            textAnchor: 'end',
          })}
        />
          <AxisLeft
          scale={stockValueScale}
          stroke={primaryColor}
          axisClassName="sup"
          tickStroke={primaryColor}
          tickLabelProps={() => ({
            fill: primaryColor,
            stroke: "white",
            fontSize: 15,
            textAnchor: 'start',
            dx: '15px',
            dy: '-2px'
          })}   
      />   
        
        </svg>
        {tooltipData && (
          <div>
            <Tooltip top={tooltipTop - 12} left={tooltipLeft + 12} style={tooltipStyles}>
              {`${getStockValue(tooltipData)}`}
            </Tooltip>
            <Tooltip

              top={yMax - 14}
              left={tooltipLeft}
              style={{
                ...defaultStyles,
                
                minWidth: 72,
                textAlign: 'center',
                transform: 'translateX(-50%)',
              }}
            >
              {formatDate(getDate(tooltipData))}
            </Tooltip>
          </div>
        )}
        </div>
 
    );
  },
);