import React  from 'react'
import styled from 'styled-components';
import { Card, Grid, Header } from '../../style/components';
import CountUp from 'react-countup';
import allImg from '../../assets/empty.svg';
import pendingImg from '../../assets/empty.svg';
import acceptedImg from '../../assets/empty.svg';
import rejectedImg from '../../assets/empty.svg';


const panels = [
  { bg: `linear-gradient(-30deg, #1ecb7d 10%, rgba(30, 203, 180, 0.7) 100%), url(${allImg})`, mobile: false, name: "totalGames", text: "Active&nbsp;Garages",  icon: "gamepad" },
  { bg: `linear-gradient(-20deg, #fc6076 0%, rgba(255, 154, 68, 0.7) 100%) ,url(${pendingImg})`, mobile: true, name: "gamesPending", text: "Active&nbsp;Announcements", icon: "dot circle" },
  { bg: `linear-gradient(-20deg, #21D4FD 0%, rgba(32, 100, 50, 0.75) 100%), url(${acceptedImg})`, mobile: false, name: "gamesScheduled", text: "Active&nbsp;Events", icon: "checked calendar" },
  { bg: `linear-gradient(-50deg, #921ecb 0%, rgba(140, 95, 163, 0.86) 100%), url(${rejectedImg}) `, mobile: false, name: "gamesRejected", text: "Active Members", icon: "calendar times" },
 
]
const Panel = ({ style }: any) => {
  return (
    <Grid style={style} customCols="repeat(4, 1fr)" gridGap="0 1em" width="100%"  placeItems="stretch" mobileCols={"1fr auto"}>

      {panels.map(({ bg, gs, text, name, mobile, icon }: any) =>
        <Card hover   
          color="white"
          style={{ backgroundImage: bg, cursor: "pointer", backgroundSize: "cover" }}
          hideOnMobile={!mobile}
    
        >
          <div className="txt-center bold" style={{fontSize: ".9em", color: "white"}}>
          
            <span dangerouslySetInnerHTML={{__html: String(text)}} />

          </div>
          <Number className="grid-box bold" ><CountUp  duration={2} end={10} /></Number>
        </Card>)}
    </Grid>);
}

const Number = styled.div`
  font-size: 60px;
  color: white;`;

  export default Panel;