import styled from 'styled-components';
import React from 'react';
import { primaryColor as PC, txtColor as TC} from '../../config';
import { sizeStyle, fontStyle, designStyle, elementProps } from '../generator';
import '../main.css';

export const Button = (props: any) => {
    const darkMode = false;
    let { invert, bg, onSubmit, children, padding, minWidth, margin, disabled, loading, primaryColor, txtColor } = props;

    primaryColor = primaryColor || PC;

    const { color, size, bold, textAlign,  placeSelf, textShadow } = props;
    const fontProps = { color, size, bold, textAlign,  placeSelf, textShadow };
   
    const Bt = styled.button`
    ${designStyle({ ...props, ...{ cursor: true } })}
    ${sizeStyle({ ...props, ...{ margin: margin || '0.15em', padding: padding || "0.3em 0.4em", minWidth: minWidth || '6.5em' } })}
    ${fontStyle(fontProps, darkMode)}
    ${`background:  ${bg || darkMode ? bg || "white" : primaryColor};`}
    border: 2px solid ${bg || primaryColor}; 
    color : ${bg? "white" : color || "white"};
    ${!disabled? `&:hover {
        background: ${!invert? color || "white" : bg || primaryColor};
        color: ${bg? invert? "white": bg: !invert? primaryColor : color || "white"};
        box-shadow: 0 0 0 0;
        transform: scale(0.99);
        transition: 350ms ease-out;
    }` : ''}
    &:focus{outline:0;}
    ${invert ?  `background: ${"white"}; color: ${primaryColor};` : ''}
    ${bg && `${invert && `background: white; color: ${bg !== 'white' ? bg : primaryColor}`}`}
    ${disabled? `background: #F0F3F4; border-color: #F0F3F4; color: ${"darkgray"};`: ''} `;

    return (<Bt disabled={disabled} onSubmit={onSubmit} {...elementProps(props)}>
        {loading && <>
                        <i className="fas fa-spinner spin"></i> &nbsp;
                    </>}
        {children}</Bt>);
};

export default Button;
