import moment, {Moment} from 'moment';
type Data = {
    date: string,
    value: number
  };
const createPointsFactory = (randomCloseGenerator : any) => {
  return (start_ : Moment, end_: Moment) => {
    let start = moment(start_)
      .set('hours', 0)
      .set('minutes', 0)
      .set('seconds', 0)
      .set('milliseconds', 0);

    let end = moment(end_)
      .set('hours', 0)
      .set('minutes', 0)
      .set('seconds', 0)
      .set('milliseconds', 0);
    
    if (end.isBefore(start, 'day')) {
      throw new Error('start <= end');
    }

    let t = end.diff(start, 'day');

    const points : Data[]= [ {date: start.clone().toString(), value: randomCloseGenerator()} ];
    while (--t >= 0) {
      start = start.add(1, 'day');
      points.push({date: start.clone().toString(), value: randomCloseGenerator()});
    }
    return points;    
  }
}

const randomCloseFactory = (maxRandomPoint: number) => () : number => Math.floor(Math.random() * maxRandomPoint) + 200;


const maxClose = 50;
export const createPoints = createPointsFactory(randomCloseFactory(maxClose));
