import styled from 'styled-components';
import React, { useContext } from 'react';
import { sizeStyle, fontStyle, designStyle, elementProps, gridStyle, FontStyle, SizeStyle, ElementProps, GridStyle } from '../generator';

interface GridProps extends FontStyle, SizeStyle, ElementProps, GridStyle{
    children? : React.ReactNode;
    background? : string; 
}

export const Grid = (props: GridProps) => {
    const { children, background } = props;
    const darkMode = false;
    const elProps = elementProps(props);
     const Layout = styled.div`
        ${background && `background: ${background};`}
        ${gridStyle(props)}
        ${sizeStyle(props)}
        ${fontStyle(props, darkMode)}`;
     return (<Layout {...elProps}>{children}</Layout>);
 };



export const GridItem = (props: any) => {
    const { children, gridRowStart, gridColumnStart, background, hideOnMobile } = props;
    const darkMode = true;
    const elProps = elementProps(props);
    const Layout = styled.div`
    ${sizeStyle(props)}
     ${gridStyle(props)}   
     ${gridRowStart && `grid-row-start: ${gridRowStart};`}
     ${gridColumnStart && `grid-column-start: ${gridColumnStart};`}
     ${background && `background: ${background};`}
     ${fontStyle(props, darkMode)}
     ${designStyle(props)}`;
    return (<Layout {...elProps}> {children} </Layout>);
};
