import React from 'react';
import { Analytics } from './pages';
import Sidebar from './style/components/sidebar';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import TopNavBar from './style/components/topnav';
import './style/main.css';
import 'antd/dist/antd.css';

function App() {
  return (
    <div 
      className="App" 
      style={{position: "absolute", top: 0, left: 0, padding: 0, margin: 0, paddingLeft: "15em", paddingTop: "4.5em", background: "#EEF3F3", minWidth: "100vw", minHeight: "100vh", boxSizing: "border-box"}}>
      <Router>
        
        <Sidebar />
        <TopNavBar />
        <Switch>
          <Route path="/" render={() => <Analytics />} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
