import React, { useRef, useEffect, useState } from 'react';
import VX from './vx-demo';
import { Grid } from '../../style/components';
import { Card, Text, Button } from '../../style/components';
import { primaryColor } from '../../config';
import '../../style/main.css'
import { DatePicker, Select } from 'antd';
import 'antd/dist/antd.css';
import moment, { Moment } from 'moment';
import { createPoints } from './data-generator';
import Panel from './panel';

const Analytics = () => {
  const [ el, setter] = useState({x: 0, y : 0});
  const container = useRef<any>(null); 
  const [range, setRange] = useState<any>({
    startDate: moment("2010-2-2", 'YYYY-MM-DD'),
    endDate: moment("2010-3-2", 'YYYY-MM-DD')
  })
  const [stock, setStocks] = useState(createPoints(range.startDate, range.endDate));
  const setEndDate = (date : Moment)=>{
    if(date < range.startDate){
        setRange({
          startDate: date,
          endDate: range.startDate
        })
    } else setRange({...range, endDate : date})
}

const setStartDate = (date : Moment)=>{
  if(date > range.endDate){
      setRange({
        startDate: range.endDate,
        endDate: date
      })
  } else setRange({...range,startDate : date})
}

useEffect(()=> setStocks(createPoints(range.startDate, range.endDate)), [range]);
  useEffect(()=>{
    if(container?.current.clientHeight){
      const {clientWidth: x, clientHeight: y} =  container.current;
      setter({ x, y});
    }
  }, [container]);

  return (
    <Grid>
      <div ref={container} style={{ maxWidth: "80vw", maxHeight: "35vh", minHeight: "300px", minWidth: "40vw" }}>
        <Panel />
      <Card grid customCols={"1fr auto"} bgColor={primaryColor}>
        <VX width={el.x} height={el.y} stock={stock}/>
        <FilterBox range={range} setEndDate={setEndDate} setStartDate={setStartDate}/>
        </Card>
      </div>
    </Grid>
  );
}


const FilterBox = ({ setEndDate, range, setStartDate } : any)=>(
  <div 
  style={{
  display: "grid", 
  height: "100%", 
  borderLeft: "1px solid lightgray",
  gridAutoColumns: "auto auto auto auto 1fr", 
  padding: ".5em 1em .5em 1.5em",
  placeItems: "end start"}}
  >
    <Text width="100%" color="white" textAlign="center" size={2} bold>Filter</Text>
    <Text color="white" textAlign="left" bold>Select Garage</Text>
    
    <Select
      disabled
      defaultValue={"Garage A"}
      style={{ borderRadius: 10, width: "100%", placeSelf: "end" }}
    />
    <Text color="white" textAlign="left" margin=".5em 0 0 0" bold>Start Date</Text>

    <DatePicker
      style={{ borderRadius: 8}}
      onChange={(e: any) => setStartDate(e)}
      value={range.startDate}
    />
   <Text color="white" bold margin=".5em 0 0 0">End Date</Text>
    <DatePicker
      style={{ borderRadius: 8}}
      onChange={(e: any) => setEndDate(e)}
      value={range.endDate}
    />
    <Button shadow bg="teal" margin=".8em 0 0 0" style={{placeSelf: "end stretch"}}>Generate Data</Button>
  </div> 
)

export default Analytics;
